#skills {
    padding: 3rem 2rem;
    color: #eae0d5;
    text-align: left;
}

.skillTitle {
    text-align: center;
    font-size: 3rem;
    margin-bottom: 2rem;
}

.column-about {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 1rem;
    box-sizing: border-box;
}

.desc-container {
    margin-bottom: 2rem;
    font-size: 1.2rem;
    line-height: 1.6;
}

.accordion-container {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.accordion {
    background-image: linear-gradient(135deg, #99bc8f, #627a55);
    color: #333;
    border: 1px solid #ccc;
    border-radius: 5px;
    overflow: hidden;
    transition: background-color 0.3s;
}

.accordion-header {
    padding: 1rem;
    cursor: pointer;
    color: #fff;
    transition: background-color 0.3s, color 0.3s;
}

.accordion-header:hover {
    background-color: #eae0d5;
    color: #627a55;
}

.accordion-content {
    padding: 1rem;
    display: none;
    font-size: 1rem;
    line-height: 1.5;
}

.accordion-content a {
    color: #627a55;
    text-decoration: none;
}

.accordion-content a:hover {
    text-decoration: underline;
}

.accordion.open .accordion-content,
.accordion:hover .accordion-content {
    display: block;
}

/* Media queries for responsiveness */
@media (max-width: 768px) {
    .skillTitle {
        font-size: 2.5rem;
    }

    .desc-container {
        font-size: 1.1rem;
    }

    .accordion-header {
        font-size: 1rem;
    }

    .accordion-content {
        font-size: 0.9rem;
    }
}

@media (max-width: 480px) {
    .skillTitle {
        font-size: 2rem;
    }

    .desc-container {
        font-size: 1rem;
    }

    .accordion-header {
        font-size: 0.9rem;
        padding: 0.75rem;
    }

    .accordion-content {
        font-size: 0.85rem;
        padding: 0.75rem;
    }
}
